<template>
	<view class="camera-page">
		<!-- Áõ∏Êú∫ÁªÑ‰ª∂ÔºàÂÖ®Â±èÔºâ -->
		<camera class="camera" :device-position="cameraPosition" :flash="flash" v-if="showCamera">
			<!-- ÂÄíËÆ°Êó∂ -->
			<cover-view class="countdown" v-if="countdown > 0">
				<cover-view class="countdown-number">{{ countdown }}</cover-view>
			</cover-view>
		</camera>

		<!-- ÂßøÂäøÂèÇËÄÉÂõæÔºàÂ±Ö‰∏≠ÔºåÂçäÈÄèÊòéÔºâ -->
		<image src="/static/girl-pose-01.png" class="pose-reference" :style="{ opacity: 0.5 }" />

		<!-- Ëá™ÂÆö‰πâÈ°∂ÈÉ®ÂØºËà™Ê†è -->
		<view class="custom-navbar">
			<!-- ËøîÂõûÊåâÈíÆ -->
			<view class="nav-left" @click="goBack">
				<image src="/static/arrow_left.png" class="nav-icon" />
			</view>
		</view>

		<!-- Âõ∫ÂÆöÂú®Â∫ïÈÉ®ÁöÑÊìç‰ΩúÊ†è -->
		<view class="fixed-action-bar">
			<!-- ÂßøÂäøÈÄâÊã©ÊèêÁ§∫Ê°Ü -->
			<view class="pose-tip" v-if="showPoseTip" :class="{ 'fade-out': isTipFading }" @click="hidePoseTip">
				<view class="tip-content">
					<text class="tip-icon">üëá</text>
					<text class="tip-text">Ë∞ÉÊï¥ÂèÇÊï∞</text>
				</view>
				<view class="tip-arrow"></view>
			</view>
			<view class="action-items">
				<!-- Â∑¶‰æßÔºöÁõ∏ÂÜåÈ¢ÑËßà -->
				<view class="action-item album-preview">
					<image src="/static/girl-pose-01.png" class="preview-image" />
				</view>

				<!-- ‰∏≠Â§ÆÔºöÊãçÁÖßÊåâÈíÆ -->
				<view class="action-item shutter-container" @click="takePhoto">
					<view class="shutter-button">
						<view class="shutter-circle">
							<view class="loading" v-if="isCapturing"></view>
						</view>
					</view>
				</view>

				<!-- Âè≥‰æßÔºöÂàáÊç¢ÊëÑÂÉèÂ§¥ -->
				<view class="action-item switch-camera" @click="switchCamera">
					<image src="/static/switch_camera.png" class="switch-icon" />
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				showCamera: true,
				cameraPosition: 'back',
				flash: 'off',
				isCapturing: false,
				countdown: 0,
				showPoseTip: false, // ÊéßÂà∂ÊèêÁ§∫Ê°ÜÊòæÁ§∫
				isTipFading: false, // ÊéßÂà∂Ê∑°Âá∫Âä®Áîª
				tipTimer: null, // ÊèêÁ§∫Ê°ÜÂÆöÊó∂Âô®
			}
		},
		// Ê∑ªÂä† onReady ÁîüÂëΩÂë®Êúü
		onReady() {
			// È°µÈù¢Ê∏≤ÊüìÂÆåÊàêÂêéÊòæÁ§∫ÊèêÁ§∫
			this.showPoseSelectionTip();
		},

		// Âú® onHide ‰∏≠Ê∏ÖÁêÜÂÆöÊó∂Âô®
		onHide() {
			this.clearTipTimer();
		},

		// Âú® onUnload ‰∏≠Ê∏ÖÁêÜÂÆöÊó∂Âô®
		onUnload() {
			this.clearTipTimer();
		},
		methods: {
			switchCamera() {
				this.cameraPosition = this.cameraPosition === 'back' ? 'front' : 'back';
			},
			takePhoto() {
				// ÊãçÁÖßÈÄªËæë
				if (this.isCapturing) return;
				this.isCapturing = true;
				this.startCountdown();
			},
			goBack() {
				uni.navigateBack();
			},
			// ÂÄíËÆ°Êó∂
			startCountdown() {
				this.countdown = 3;
				const timer = setInterval(() => {
					this.countdown--;

					if (this.countdown <= 0) {
						// ÈúáÂä®ÂèçÈ¶à
						uni.vibrateShort();
						clearInterval(timer);
						this.capturePhoto();
					}
				}, 1000);
			},
			// ÊâßË°åÊãçÁÖß
			capturePhoto() {
				const ctx = uni.createCameraContext();

				ctx.takePhoto({
					quality: 'high',
					success: (res) => {
						this.capturedImage = res.tempImagePath;
						this.isCapturing = false;

						// Êí≠ÊîæÊãçÁÖßÈü≥Êïà
						this.playShutterSound();
					},
					fail: (err) => {
						console.log('ÊãçÁÖßÂ§±Ë¥•:', err);
						this.isCapturing = false;
						uni.showToast({
							title: 'ÊãçÁÖßÂ§±Ë¥•',
							icon: 'none'
						});
					}
				});
			},

			// Êí≠ÊîæÂø´Èó®Èü≥Êïà
			playShutterSound() {
				const audioContext = uni.createInnerAudioContext();
				audioContext.src = '/static/audio/shutter.mp3';
				audioContext.play();
			},

			// ÊòæÁ§∫ÂßøÂäøÈÄâÊã©ÊèêÁ§∫
			showPoseSelectionTip() {
				// Âª∂Ëøü500msÊòæÁ§∫ÔºåÁ°Æ‰øùÈ°µÈù¢ÂÆåÂÖ®Âä†ËΩΩ
				setTimeout(() => {
					this.showPoseTip = true;

					// 2ÁßíÂêéÂºÄÂßãÊ∑°Âá∫Âä®Áîª
					this.tipTimer = setTimeout(() => {
						this.startTipFadeOut();
					}, 2000);
				}, 500);
			},

			// ÂºÄÂßãÊ∑°Âá∫Âä®Áîª
			startTipFadeOut() {
				this.isTipFading = true;

				// Âä®ÁîªÂÆåÊàêÂêéÈöêËóèÊèêÁ§∫Ê°Ü
				setTimeout(() => {
					this.showPoseTip = false;
					this.isTipFading = false;
				}, 300); // 300ms Âä®ÁîªÊó∂Èó¥
			},

			// Ê∏ÖÁêÜÂÆöÊó∂Âô®
			clearTipTimer() {
				if (this.tipTimer) {
					clearTimeout(this.tipTimer);
					this.tipTimer = null;
				}
			},

			// ÊâãÂä®ÈöêËóèÊèêÁ§∫Ê°ÜÔºàÂèØÈÄâÔºåÁî®Êà∑ÁÇπÂáªÊèêÁ§∫Ê°ÜÊó∂Ôºâ
			hidePoseTip() {
				this.clearTipTimer();
				this.startTipFadeOut();
			}
		}
	}
</script>

<style lang="scss" scoped>
	.camera-page {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		background-color: #000;
	}

	.camera {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 1;
	}

	.countdown {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);

		.countdown-number {
			color: white;
			font-size: 120rpx;
			font-weight: bold;
			text-shadow: 0 0 20rpx rgba(0, 0, 0, 0.8);
			animation: countdownPulse 1s ease-in-out;
		}
	}

	@keyframes countdownPulse {
		0% {
			transform: scale(0.5);
			opacity: 0;
		}

		50% {
			transform: scale(1.2);
			opacity: 1;
		}

		100% {
			transform: scale(1);
			opacity: 1;
		}
	}

	.pose-reference {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 80%;
		height: 70%;
		z-index: 2;
	}

	.custom-navbar {
		position: absolute;
		top: var(--status-bar-height);
		left: 0;
		right: 0;
		height: 90rpx;
		/* Ëá™ÂÆö‰πâÂØºËà™Ê†èÈ´òÂ∫¶ */
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 10rpx;
		z-index: 3;
	}

	.nav-left,
	.nav-right {
		width: 60rpx;
		height: 60rpx;
		display: flex;
		align-items: center;
		justify-content: center;
		padding-top: 50rpx;
	}

	.nav-icon {
		width: 40rpx;
		height: 40rpx;
	}

	/* Âõ∫ÂÆöÂú®Â∫ïÈÉ®ÁöÑÊìç‰ΩúÊ†è */
	.fixed-action-bar {
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: 100;
		width: 100%;
		background: rgba(0, 0, 0, 0.8);
		backdrop-filter: blur(20px);
		border-top: 1rpx solid rgba(255, 255, 255, 0.1);
		padding: 40rpx 0;
	}

	// ÂßøÂäøÈÄâÊã©ÊèêÁ§∫Ê°Ü
	.pose-tip {
		position: absolute;
		bottom: 150rpx;
		left: 80rpx;
		z-index: 200;
		animation: tipFadeIn 0.3s ease-out;

		&.fade-out {
			animation: tipFadeOut 0.3s ease-out;
		}

		.tip-content {
			background: white;
			color: black;
			padding: 10rpx 15rpx;
			border-radius: 10rpx;
			font-size: 24rpx;
			display: flex;
			align-items: center;
			gap: 10rpx;
			backdrop-filter: blur(10rpx);
			border: 1rpx solid rgba(255, 255, 255, 0.2);
			box-shadow: 0 8rpx 32rpx rgba(0, 0, 0, 0.3);

			.tip-icon {
				font-size: 24rpx;
				animation: pointDown 1s ease-in-out infinite;
			}

			.tip-text {
				white-space: nowrap;
			}
		}

		// ÁÆ≠Â§¥ÊåáÂêëÂßøÂäøÈ¢ÑËßà
		.tip-arrow {
			width: 0;
			height: 0;
			border-left: 15rpx solid transparent;
			border-right: 15rpx solid transparent;
			border-top: 20rpx solid rgba(0, 0, 0, 0.8);
			margin: 0 auto;
			position: relative;

			&::before {
				content: '';
				position: absolute;
				top: -22rpx;
				left: -16rpx;
				width: 0;
				height: 0;
				border-left: 16rpx solid transparent;
				border-right: 16rpx solid transparent;
				border-top: 22rpx solid rgba(255, 255, 255, 1);
			}
		}
	}

	// ÊèêÁ§∫Ê°ÜÂä®Áîª
	@keyframes tipFadeIn {
		0% {
			opacity: 0;
			transform: translateY(20rpx);
		}

		100% {
			opacity: 1;
			transform: translateY(0);
		}
	}

	@keyframes tipFadeOut {
		0% {
			opacity: 1;
			transform: translateY(0);
		}

		100% {
			opacity: 0;
			transform: translateY(-20rpx);
		}
	}

	// ÊâãÊåáÊåáÂêëÂä®Áîª
	@keyframes pointDown {
		0% {
			transform: translateY(-5px);
		}

		50% {
			transform: translateY(5rpx);
		}

		100% {
			transform: translateY(-5px);
		}
	}

	/* Êìç‰ΩúÈ°πÂÆπÂô® */
	.action-items {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0 60rpx;
		gap: 70rpx;
	}

	/* ÊØè‰∏™Êìç‰ΩúÈ°πÁöÑÈÄöÁî®Ê†∑Âºè */
	.action-item {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100rpx;
	}

	/* Áõ∏ÂÜåÈ¢ÑËßà */
	.album-preview {
		width: 80rpx;
		height: 80rpx;
		border-radius: 16rpx;
		overflow: hidden;
		border: 3rpx solid rgba(255, 255, 255, 0.8);

		.preview-image {
			width: 100%;
			height: 100%;
		}
	}

	/* ÊãçÁÖßÊåâÈíÆÂÆπÂô® */
	.shutter-container {
		flex: 1;
		max-width: 200rpx;
	}

	/* ÊãçÁÖßÊåâÈíÆ */
	.shutter-button {
		width: 120rpx;
		height: 120rpx;
		border-radius: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		background: rgba(255, 255, 255, 0.2);
	}

	.shutter-circle {
		width: 80rpx;
		height: 80rpx;
		border-radius: 50%;
		background: #ffffff;
		border: 4rpx solid rgba(255, 255, 255, 0.6);
		transition: all 0.1s ease;
		display: flex;
		align-items: center;
		justify-content: center;

		.shutter-button:active & {
			transform: scale(0.95);
			opacity: 0.8;
		}

		.loading {
			width: 40rpx;
			height: 40rpx;
			border: 4rpx solid #ccc;
			border-top: 4rpx solid #333;
			border-radius: 50%;
			animation: spin 1s linear infinite;
		}
	}

	@keyframes spin {
		0% {
			transform: rotate(0deg);
		}

		100% {
			transform: rotate(360deg);
		}
	}

	/* ÂàáÊç¢ÊëÑÂÉèÂ§¥ÊåâÈíÆ */
	.switch-camera {
		width: 80rpx;
		height: 80rpx;
		display: flex;
		justify-content: center;
		align-items: center;
		background: rgba(255, 255, 255, 0.15);
		border-radius: 50%;
	}

	.switch-icon {
		width: 40rpx;
		height: 40rpx;
		filter: brightness(200%);
	}
</style>